.weather {
  border: solid 1px #d5d9d4;
  border-radius: 5px;
  padding: 20px;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  width: 750px;
}

.weather__title {
  margin: 0 0 30px;
  font-size: 35px;
  line-height: 1.25;
  font-weight: 900;
  position: relative;
  z-index: 1;
}

.weather__info {
  margin: 0;
  font-size: 26px;
  line-height: 1.25;
  font-weight: 500;
  z-index: 1;
}

.weather__span {
  font-size: 26px;
  line-height: 1.25;
  font-weight: 900;
  z-index: 1;
}

.weather__two-columns {
  display: flex;
  justify-content: space-between;
}

.weather__column {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.weather__background {
  background-image: url("../../images/background-base.svg");
  background-size: 200px;
  background-repeat: repeat;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  will-change: transform;
  transform: translate3d(0, calc(var(--scrollTop) / 6), 0);
}

.weather__background_sun {
  background-image: url("../../images/weather-background-sun.svg");
  background-size: 400px;
}

.weather__background_cloud {
  background-image: url("../../images/weather-background-cloud.svg");
  background-size: 400px;
}

.weather__background_snow {
  background-image: url("../../images/weather-background-snow.svg");
  background-size: 400px;
}

@media screen and (max-width: 800px) {
  .weather {
    max-width: 400px;
    padding: 30px;
    margin: 5px;
  }

  .weather__two-columns {
    flex-direction: column;
    justify-content: baseline;
    gap: 20px;
  }

  .weather__title {
    font-size: 26px;
  }

  .weather__info {
    font-size: 20px;
  }

  .weather__span {
    font-size: 20px;
  }
}

@media screen and (max-width: 500px) {
  .weather {
    width: calc(100% - 10px);
    padding: 20px;
  }
}
